import React, { useState, useContext, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCheck, faKey, faFingerprint, faInfoCircle, faSync, faCloud, faClock } from '@fortawesome/free-solid-svg-icons';
import { getAAGUIDInfo } from '../../utils/aaguid';
import { PasskeysContext } from '../../context/PasskeysState';
import { handleAuth } from '../../utils/auth';
import "../../styles/dashboard.scss";

const Dashboard = ({ apiCall, registration, email, setCookie }) => {
  const { onDarkMode, setUser, setAuthError } = useContext(PasskeysContext);
  const modeClasses = onDarkMode ? "dark-mode-settings" : "light-mode-settings";
  
  const [aaguidInfo, setAAGUIDInfo] = useState(null);
  const [loading, setLoading] = useState(true);
  const [testingAuth, setTestingAuth] = useState(false);
  const [testSuccess, setTestSuccess] = useState(false);
  const [activeTooltip, setActiveTooltip] = useState(null);

  // Get AAGUID info to display provider name
  useEffect(() => {
    async function fetchAAGUIDInfo() {
      if (registration && registration.aaguid) {
        const info = await getAAGUIDInfo(registration.aaguid);
        setAAGUIDInfo(info);
      }
      setLoading(false);
    }

    fetchAAGUIDInfo();
  }, [registration]);

  // Get creation time (formatted)
  const getFormattedDate = () => {
    const now = new Date();
    return now.toLocaleDateString('en-US', { 
      month: 'long', 
      day: 'numeric', 
      year: 'numeric' 
    }) + ' – ' + now.toLocaleTimeString('en-US', {
      hour: 'numeric',
      minute: '2-digit',
      hour12: true
    }).toLowerCase();
  };

  const handleTestAuth = () => {
    setTestingAuth(true);
    setTestSuccess(false);
    
    // Create an array with the current registration
    const credentials = [{
      id: registration.rawId,
      type: registration.type
    }];
    
    // Test authentication
    handleAuth(
      apiCall,
      (userData) => {
        setUser(userData);
        setTestingAuth(false);
        setTestSuccess(true);
        setTimeout(() => {
          setTestSuccess(false);
        }, 3000); // Reset after 3 seconds
      },
      (error) => {
        setAuthError(error);
        setTestingAuth(false);
      },
      credentials,
      email,
      setCookie
    );
  };

  // Determine if the passkey is synced (based on backupEligible property)
  const isBackupEligible = registration.response && 
    registration.response.getClientExtensionResults && 
    registration.response.getClientExtensionResults().credProps && 
    registration.response.getClientExtensionResults().credProps.rk;

  return (
    <motion.div 
      className={`dashboard-card ${modeClasses}`}
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -20 }}
      transition={{ duration: 0.4 }}
    >
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.2 }}
        className="welcome-message"
      >
        <h1>You're signed up with a passkey</h1>
        <p>Sign in with the same gesture that unlocks your device.</p>
      </motion.div>
      
      <div className="dashboard-content">
        {/* Metadata Panel */}
        <motion.div 
          className="metadata-panel"
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.3 }}
        >
          <h2>
            <FontAwesomeIcon icon={faKey} className="section-icon" />
            Passkey Details
          </h2>

          <div className="metadata-grid">
            <div className="metadata-item">
              <div className="metadata-header">
                <FontAwesomeIcon icon={faCloud} className="icon" />
                <span>Synced Across Devices</span>
                <div 
                  className="info-icon"
                  onMouseEnter={() => setActiveTooltip('sync')}
                  onMouseLeave={() => setActiveTooltip(null)}
                >
                  <FontAwesomeIcon icon={faInfoCircle} />
                  {activeTooltip === 'sync' && (
                    <div className="tooltip">
                      Indicates whether this passkey can be used across your devices with the same provider.
                    </div>
                  )}
                </div>
              </div>
              <div className="metadata-value">
                {isBackupEligible ? "Yes" : "No"}
              </div>
            </div>

            <div className="metadata-item">
              <div className="metadata-header">
                <FontAwesomeIcon icon={faFingerprint} className="icon" />
                <span>Passkey Provider</span>
                <div 
                  className="info-icon"
                  onMouseEnter={() => setActiveTooltip('provider')}
                  onMouseLeave={() => setActiveTooltip(null)}
                >
                  <FontAwesomeIcon icon={faInfoCircle} />
                  {activeTooltip === 'provider' && (
                    <div className="tooltip">
                      The provider where your passkey is saved.
                    </div>
                  )}
                </div>
              </div>
              <div className="metadata-value">
                {loading ? (
                  <span className="loading-pulse"></span>
                ) : (
                  <span>{aaguidInfo?.name || "Unknown Provider"}</span>
                )}
              </div>
            </div>
            
            <div className="metadata-item">
              <div className="metadata-header">
                <FontAwesomeIcon icon={faClock} className="icon" />
                <span>Creation Time</span>
                <div 
                  className="info-icon"
                  onMouseEnter={() => setActiveTooltip('time')}
                  onMouseLeave={() => setActiveTooltip(null)}
                >
                  <FontAwesomeIcon icon={faInfoCircle} />
                  {activeTooltip === 'time' && (
                    <div className="tooltip">
                      When you created this passkey.
                    </div>
                  )}
                </div>
              </div>
              <div className="metadata-value">
                {getFormattedDate()}
              </div>
            </div>
          </div>
        </motion.div>
        
        {/* Re-authentication Test */}
        <motion.div 
          className="test-auth-panel"
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.4 }}
        >
          <h2>
            <FontAwesomeIcon icon={faSync} className="section-icon" />
            Test Your Passkey
          </h2>
          
          <p>Test your passkey here. You'll be asked to present your face, fingerprint, or device PIN.</p>

          <div>
            <AnimatePresence>
              {testSuccess && (
                <motion.div 
                  className="success-message"
                  initial={{ opacity: 0, scale: 0.8 }}
                  animate={{ opacity: 1, scale: 1 }}
                  exit={{ opacity: 0 }}
                >
                  <FontAwesomeIcon icon={faCheck} className="success-icon" />
                  <span>Authentication successful!</span>
                </motion.div>
              )}
            </AnimatePresence>
          </div>
          
          <div className="test-container">
            <button 
              className={`test-button ${modeClasses}`}
              onClick={handleTestAuth}
              disabled={testingAuth}
            >
              {testingAuth ? "Waiting for Authentication..." : "Sign In With Passkey"}
            </button>
          </div>
        </motion.div>
      </div>
    </motion.div>
  );
};

export default Dashboard;
