export const PasskeysReducer = (state, action) => {
  switch(action.type) {
    case 'TOGGLE_DARK_MODE':
      return {
        ...state,
        onDarkMode: !state.onDarkMode
      };
    case 'SET_USER':
      return {
        ...state,
        user: action.payload
      };
    case 'SET_AUTH_ERROR':
      return {
        ...state,
        authError: action.payload
      };
    default:
      return state;
  }
}

export default PasskeysReducer;