import axios from 'axios';
import {
    parseCreationOptionsFromJSON,
    create,
    get,
    parseRequestOptionsFromJSON,
    supported,
    AuthenticationPublicKeyCredential,
} from "@github/webauthn-json/browser-ponyfill";

const Buffer = require('buffer/').Buffer;
const cbor = require("cbor");

const apiUrl = process.env.REACT_APP_API_URL;

const useApiCall = (cookies) => {
    const apiCall = axios.create({
        baseURL: apiUrl,
        headers: {
            Authorization: '',
        }
    });

    apiCall.interceptors.request.use(
        async (config) => {
            if (cookies && cookies.id != null && cookies.token != null) {
                config.headers.Authorization = `Bearer ${cookies.token}`;
            }
            return config;
        },
        (error) => {
            console.log("Issue:", error)
            return Promise.reject(error);
        }
    );

    return apiCall;
};

const currentTime = () => {
    var currentDate = new Date();
    return (('0' + currentDate.getHours()).slice(-2) + ":" + ('0' + currentDate.getMinutes()).slice(-2));
}

const getFlagsFromAuthData = (authData) => {
    // Flags are at the 33rd byte
    const flagsByte = authData[32];

    // Convert the flags byte to a binary string
    const flagsBinary = flagsByte.toString(2).padStart(8, '0');

    // Extract flags
    const userPresent = flagsBinary[7] === '1'; // User Present
    const userVerified = flagsBinary[6] === '1'; // User Verified
    const attestedCredDataPresent = flagsBinary[5] === '1'; // Attested Credential Data Included
    const extensionDataPresent = flagsBinary[4] === '1'; // Extension Data Included

    return {
        userPresent,
        userVerified,
        attestedCredDataPresent,
        extensionDataPresent
    };
}

const getAAGUIDFromAuthData = (authData) => {
    // Flags are at the 33rd byte, AT flag is the fifth bit from the right
    const atFlag = !!(authData[32] & 0x40);

    // If AT flag is set, extract AAGUID
    if (atFlag) {
        // AAGUID starts at the 37th byte and is 16 bytes long
        const aaguidBuffer = authData.slice(37, 37 + 16);
        return bufferToHex(aaguidBuffer); // Convert the buffer to a hexadecimal string
    } else {
        return null; // AAGUID not present
    }
}

const bufferToHex = (buffer) => {
    return Array.prototype.map.call(new Uint8Array(buffer), x => x.toString(16).padStart(2, '0')).join('');
}

const handleSplash = async (apiCall, setUser, cookies) => {
    if (cookies && cookies.id != null && cookies.token != null) {
        apiCall.get(`/users/${cookies.id}`)
        .then((res) => {
            setUser(res.data);
        })
    }
}

const handleAuth = async (apiCall, onSuccess, onError, registeredCredentials, username, setCookie) => {
    if (registeredCredentials.length > 0) {
        const cro = parseRequestOptionsFromJSON({
            publicKey: {
            challenge: "CCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCC",
            allowCredentials: registeredCredentials,
            userVerification: "required",
            },
        });

        // apiCall.post(`/login/`, { username: username, auth: get(cro) })
        get(cro)  // only for demo site
        .then((res) => {
            // setCookie(res.data);
            const userData = {  // only for demo site
                registrations: [res.toJSON()]
            };
            onSuccess(userData);
            if (typeof onSuccess !== 'function') {
                console.warn('onSuccess callback is not a function');
            }
        })
        .catch((e) => {
            console.log("Error:", e);
            if (typeof onError === 'function') {
                onError(`Error: ${e.message || 'Unknown error'}`);
            }
        });
    } else {
        // Convert email to a safe user ID by encoding it
        const safeUserId = btoa(username).replace(/[+/=]/g, ''); // base64 encode and remove special chars
        
        const cco = parseCreationOptionsFromJSON({
            publicKey: {
              challenge: "CCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCCC",
              rp: { name: "calvinhklui.com" },
              user: {
                id: safeUserId, // Using the safe encoded version for the ID
                name: username,
                displayName: username,
              },
              pubKeyCredParams: [],
              excludeCredentials: registeredCredentials,
              authenticatorSelection: { userVerification: "required" },
              extensions: {
                credProps: true,
              },
            },
        });

        // only for demo site
        create(cco)
        .then((res) => {
            // setCookie(res.data);

            var registration = res.toJSON();
            var attCbor = Buffer.from(registration.response.attestationObject, 'base64');
            var attCborObj = cbor.decodeAllSync(attCbor)[0];
            const flags = getFlagsFromAuthData(attCborObj.authData);
            const aaguid = getAAGUIDFromAuthData(attCborObj.authData);

            registration.attestationObject = attCborObj;
            registration.flags = flags;
            registration.aaguid = aaguid;
            
            console.log("REG:", registration);

            const userData = {  // only for demo site
                registrations: [registration]
            };
            
            if (typeof onSuccess === 'function') {
                onSuccess(userData);
            } else {
                console.warn('onSuccess callback is not a function');
            }
        })
        .catch((e) => {
            console.log("Error:", e);
            if (typeof onError === 'function') {
                onError(e.message || 'Unknown error');
            }
        });
    }
};

const handleLogout = async (apiCall, setUser, setAuthError, cookies, removeCookie) => {
    setUser(null);  // only for demo site
    setAuthError("");  // only for demo site

    if (cookies && cookies.id && cookies.token) {
        apiCall.post(`/logout/${cookies.id}`)
        .then((res) => {
            setUser(null);
            removeCookie('id');
            removeCookie('token');
        })
        .catch((e) => {
            console.log("Error:", e);
            setAuthError(e.message);
            setUser(null);
            removeCookie('id');
            removeCookie('token');
        });
    }
}

const handleDelete = async (apiCall, setUser, setAuthError, cookies, removeCookie) => {
    setUser(null);  // only for demo site
    setAuthError("");  // only for demo site

    if (cookies && cookies.id && cookies.token) {
        apiCall.delete(`/users/${cookies.id}`)
        .then((res) => {
            setUser(null);
            removeCookie('id');
            removeCookie('token');
        })
        .catch((e) => {
            console.log("Error:", e);
            setAuthError(e.message);
        });
    }
}

export { useApiCall, handleSplash, handleAuth, handleLogout, handleDelete };