import React, { createContext, useReducer } from 'react';
import PasskeysReducer from './PasskeysReducer';

const initialPasskeysState = {
  onDarkMode: true,
  user: null,
  authError: null,
}

export const PasskeysContext = createContext(initialPasskeysState);

export const PasskeysProvider = ({ children }) => {
  const [state, dispatch] = useReducer(PasskeysReducer, initialPasskeysState);

  const toggleDarkMode = () => {
    dispatch({
      type: 'TOGGLE_DARK_MODE'
    })
  }

  const setUser = (user) => {
    dispatch({
      type: 'SET_USER',
      payload: user
    })
  }

  const setAuthError = (e) => {
    dispatch({
      type: 'SET_AUTH_ERROR',
      payload: e
    })
  }

  return (
    <PasskeysContext.Provider value={{
      onDarkMode: state.onDarkMode,
      toggleDarkMode,
      user: state.user,
      setUser,
      authError: state.authError,
      setAuthError,
    }}>
      { children }
    </PasskeysContext.Provider>
  );
}