const AAGUID_URL = 'https://raw.githubusercontent.com/passkeydeveloper/passkey-authenticator-aaguids/main/aaguid.json';

let aaguidCache = null;

export async function getAAGUIDInfo(aaguid) {
  // Format AAGUID to match database format (8-4-4-4-12)
  const formatAAGUID = (aaguid) => {
    // Remove any existing hyphens and convert to lowercase
    const clean = aaguid.replace(/-/g, '').toLowerCase();
    if (clean.length !== 32) return null;
    
    // Insert hyphens in correct positions
    return `${clean.slice(0,8)}-${clean.slice(8,12)}-${clean.slice(12,16)}-${clean.slice(16,20)}-${clean.slice(20)}`;
  };
  
  if (!aaguid) return null;
  const normalizedAAGUID = formatAAGUID(aaguid);
  if (!normalizedAAGUID) return null;

  try {
    // Fetch and cache the AAGUID data if not already cached
    if (!aaguidCache) {
      const response = await fetch(AAGUID_URL);
      if (!response.ok) throw new Error('Failed to fetch AAGUID data');
      aaguidCache = await response.json();
      
      // Check if the response is empty
      if (Object.keys(aaguidCache).length === 0) {
        console.warn('AAGUID database appears to be empty or retired');
        return null;
      }
    }

    return aaguidCache[normalizedAAGUID] || null;
  } catch (error) {
    console.error('Error fetching AAGUID info:', error);
    return null;
  }
} 