import React, { useContext, useEffect, useState, useCallback } from "react"
import { useCookies } from 'react-cookie';
import { PasskeysContext } from '../../context/PasskeysState'
import { useApiCall, handleSplash, handleAuth } from '../../utils/auth';
import Bitmoji from "../common/bitmoji"
import Footer from "../common/footer"
import useWindowSize from "../../hooks/useWindowSize";

import NavBar from "./navbar"
import Landing from "./landing";
import Signup from "./signup";
import Dashboard from "./dashboard";

const Splash = props => {
  const {
    onDarkMode,
    user,
    setUser,
    authError,
    setAuthError
  } = useContext(PasskeysContext);
  const [cookies, setCookie, removeCookie] = useCookies(['id', 'token']);
  const [showSignup, setShowSignup] = useState(false);
  const [isProcessing, setIsProcessing] = useState(false);
  const [isSuccess, setIsSuccess] = useState(false);
  const [showDashboard, setShowDashboard] = useState(false);
  const [registration, setRegistration] = useState(null);
  const [userEmail, setUserEmail] = useState('');
  const windowSize = useWindowSize();
  
  const modeClasses = onDarkMode ? "dark-mode-settings" : "light-mode-settings";
  const apiCall = useApiCall(cookies);

  useEffect(() => {
    handleSplash(apiCall, setUser, cookies, setCookie);
  }, [cookies]);

  const registeredCredentials = (user) => {
    if (user && user.registrations) {
      return user.registrations.map((reg) => ({
        id: reg.rawId,
        type: reg.type,
        aaguid: reg.aaguid,
        flags: reg.flags,
        authenticatorAttachment: reg.authenticatorAttachment,
        attestationObject: reg.response ? JSON.stringify(reg.response.attestationObject) : [],
        transports: reg.response && reg.response.transports ? reg.response.transports : [],
        userHandle: reg.response && reg.response.userHandle ? reg.response.userHandle : [],
      }));
    } else {
      return [];
    }
  }

  const handleGetStarted = () => {
    setShowSignup(true);
  };

  const handleSubmit = useCallback((email) => {
    setIsProcessing(true);
    setAuthError(null);
    setUserEmail(email);
    
    // Using setTimeout to demonstrate the loading state (can remove if not needed)
    setTimeout(() => {
      handleAuth(
        apiCall,
        (userData) => {
          setUser(userData);
          setIsProcessing(false);
          setIsSuccess(true);
          
          // Store the latest registration for the dashboard
          if (userData && userData.registrations && userData.registrations.length > 0) {
            const latestRegistration = userData.registrations[userData.registrations.length - 1];
            setRegistration(latestRegistration);
            
            // Show dashboard after a short delay for the success animation
            setTimeout(() => {
              setShowDashboard(true);
            }, 2500);
          }
        },
        (error) => {
          setAuthError(error);
          setIsProcessing(false);
        },
        registeredCredentials(user),
        email,
        setCookie
      );
    }, 500);
  }, [apiCall, setUser, setAuthError, user, setCookie]);

  return (
    <section className={`${modeClasses} section-container`}>
      <NavBar apiCall={apiCall} cookies={cookies} removeCookie={removeCookie} />
      
      <div className="content-wrapper">
        {showDashboard && registration ? (
          <Dashboard 
            apiCall={apiCall}
            registration={registration}
            email={userEmail}
            setCookie={setCookie}
          />
        ) : showSignup ? (
          <Signup
            onSubmit={handleSubmit}
            modeClasses={modeClasses}
            error={authError}
            isProcessing={isProcessing}
            isSuccess={isSuccess}
            windowSize={windowSize}
          />
        ) : (
          <Landing 
            onGetStarted={handleGetStarted}
            modeClasses={modeClasses}
          />
        )}
      </div>
      
      {/* <Bitmoji context={PasskeysContext} page="passkey" /> */}
      <Footer context={PasskeysContext} page="passkey" />
    </section>
  );
}

export default Splash;